import './App.css';
import { BrowserRouter, Route , Link } from 'react-router-dom';
import DashboardScreen from './screen/DashboardScreen';
import ProductScreen from './screen/ProductScreen';
import CartScreen from './screen/CartScreen';
import { useSelector } from 'react-redux';
import SignInScreen from './screen/SignInScreen';
import DashboardScreen2 from './screen/DashboardScreen2';
import DashboardScreen3 from './screen/DashboardScreen3';



function App() {
  const cart = useSelector((state) => state.cart)
  const {cartItems} = cart;
  const userSignin = useSelector((state) => state.userSignin)
  const {userInfo} = userSignin;
  return (
    <BrowserRouter>
    <link href="App.css"></link>
    <div className="grid-container">
      <header className="row">
        <Link to="/">All</Link>
        <Link to="/category2">Matic</Link>
        <Link to="/category3">Bebek</Link>
        <div>
          <Link to="/keranjang">
            Keranjang
            {cartItems.length > 0 && (
              <span className="badge">{cartItems.length}</span>
            )}
          </Link>
          {
            userInfo ? (
              <Link to="#">{userInfo.email}</Link>
            ) : (
              <Link to="/signin">Sign In</Link>
            )
          }          
        </div>
      </header>

      <main>
        <Route path="/" component={DashboardScreen} exact></Route>
        <Route path="/produk/:id" component={ProductScreen}></Route>    
        <Route path="/keranjang/:id?" component={CartScreen}></Route>            
        <Route path="/signin" component={SignInScreen}></Route>   
        <Route path="/category2" component={DashboardScreen2} exact></Route>
        <Route path="/category3" component={DashboardScreen3} exact></Route>     
      </main>
      <footer className="row center">Muh Nurkhaliz</footer>
    </div>
    </BrowserRouter>
  );
}

export default App;
