import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { detailsproduct } from '../action/productAction';

export default function ProductScreen(props){
    const dispatch = useDispatch();
    const productId = props.match.params.id;
    const[Qty, setQty] = useState(1)
    const productDetails = useSelector((state) => state.productDetailsList)
    const {product} = productDetails
    
    

    useEffect(() => {
        dispatch(detailsproduct(productId))
    }, [dispatch, productId])

    const addToCartHandler = () => {
        props.history.push(`/keranjang/${productId}?Qty=${Qty}`)
    }
    
    return(
        <div className="row">
            <div className="col-2">
                <img className="large" src={product.image} alt={product.name}></img>
            </div>
            <div className="col-1">
                <div className="card card-body">
                <h1>{product.name}</h1>
                <ul>
                    
                    <li>
                        <div className="row">
                            <h1>Harga</h1>
                            <div className="price">Rp. {product.price}</div>
                        </div>
                    </li>
                    <li>
                        <div className="row">
                            {product.countInStock > 0 ? (
                                <span className="success">Tersedia</span>
                            ): (
                                <span className="success">Tidak Tersedia</span>
                            )}
                        </div>
                    </li>
                    {
                        product.countInStock > 0 && 
                        (
                        <>
                            <li>
                                <div className="row">
                                    <div>
                                        Jumlah
                                    </div>
                                    <div>
                                        <select value={Qty} onChange={error => setQty(error.target.value)}>
                                            {[...Array(product.countInStock).keys()].map(
                                                (x) => (
                                                    <option key={x+1}value={x+1}>{x+1}</option>
                                                )
                                            )}
                                        </select>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <button onClick={addToCartHandler} className="primary block">Tambah di Keranjang</button>
                            </li>
                        </>
                        )
                    }
                    
                </ul>
                </div>
            </div>
        </div>
    )
}